'use client';
import { useState, useEffect } from 'react';
import { AnalysisResult } from '@/lib/types';
import { RegulatorySummary } from '@/components/RegulatorySummary';
import { ImpactAssessment } from '@/components/ImpactAssessment';
import { Timeline } from '@/components/Timeline';
import { DocumentSelector } from '@/components/DocumentSelector';
import { Bot, Loader2 } from 'lucide-radix';

export default function Home() {
  const [documents, setDocuments] = useState<string[]>([]);
  const [selectedDocument, setSelectedDocument] = useState<string>('');
  const [analysis, setAnalysis] = useState<AnalysisResult | null>(null);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // Fetch the list of available documents on component mount
    const fetchDocuments = async () => {
      try {
        const res = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/documents`);
        if (!res.ok) throw new Error('Failed to fetch documents');
        const data = await res.json();
        setDocuments(data.documents);
        if (data.documents.length > 0) {
          setSelectedDocument(data.documents[0]);
        }
      } catch (e) {
        setError(e instanceof Error ? e.message : 'An unknown error occurred.');
      }
    };
    fetchDocuments();
  }, []);

  useEffect(() => {
    // Fetch analysis when the selected document changes
    if (selectedDocument) {
      const fetchAnalysis = async () => {
        setIsLoading(true);
        setError(null);
        setAnalysis(null);
        try {
          const res = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/analyze`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ document_name: selectedDocument }),
          });
          if (!res.ok) {
            const errData = await res.json();
            throw new Error(errData.detail || 'Failed to fetch analysis.');
          }
          const data: AnalysisResult = await res.json();
          setAnalysis(data);
        } catch (e) {
          setError(e instanceof Error ? e.message : 'An unknown error occurred.');
        } finally {
          setIsLoading(false);
        }
      };
      fetchAnalysis();
    }
  }, [selectedDocument]);

  return (
    <main className="bg-gray-50 min-h-screen p-4 sm:p-8">
      <div className="max-w-7xl mx-auto">
        <header className="mb-8">
          <h1 className="text-4xl font-bold text-gray-800 tracking-tight">AI Regulatory Review</h1>
          <p className="text-lg text-gray-600 mt-2">Automated insights from financial regulatory documents.</p>
        </header>

        <div className="mb-8 flex items-center space-x-4">
          <DocumentSelector 
            documents={documents}
            selectedDocument={selectedDocument}
            onSelectDocument={setSelectedDocument}
            isLoading={isLoading}
          />
        </div>

        {isLoading && (
          <div className="flex flex-col items-center justify-center p-12 bg-white rounded-lg shadow-md border">
            <Loader2 className="h-12 w-12 text-blue-600 animate-spin" />
            <p className="mt-4 text-lg text-gray-700 font-semibold">Analyzing {selectedDocument}...</p>
            <p className="text-gray-500">This may take a moment.</p>
          </div>
        )}

        {error && (
            <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 rounded-md" role="alert">
                <p className="font-bold">Error</p>
                <p>{error}</p>
            </div>
        )}

        {analysis && !isLoading && (
          <div className="grid grid-cols-1 gap-8">
            <RegulatorySummary summary={analysis.regulatorySummary} />
            <ImpactAssessment assessment={analysis.impactAssessment} />
            <Timeline dates={analysis.keyDates} />
          </div>
        )}
      </div>
    </main>
  );
}